	module.exports = {"data":{"markdownRemark":{"html":"<h3>Reading</h3>\n<ul>\n<li><a href=\"https://www.buzzfeednews.com/article/charliewarzel/alex-jones-will-never-stop-being-alex-jones#.hiq9ZabEL\">Alex Jones Will Never Stop Being Alex Jones</a></li>\n<li><a href=\"https://www.washingtonpost.com/local/pizzagate-from-rumor-to-hashtag-to-gunfire-in-dc/2016/12/06/4c7def50-bbd4-11e6-94ac-3d324840106c_story.html?utm_term=.968a5cf86e4b\">Pizzagate: From rumor, to hashtag, to gunfirein D.C.</a></li>\n<li><a href=\"https://www.vox.com/policy-and-politics/2018/8/1/17253444/qanon-trump-conspiracy-theory-reddit\">#QAnon, the scarily pro-Trump conspiracy theory, explained</a></li>\n<li><a href=\"https://skepticalscience.com/docs/Debunking_Handbook.pdf\">The Debunking Handbook</a></li>\n</ul>\n<h3>Notes, responses</h3>\n<ul>\n<li>Part of QAnon's power lies in the flexibility of its narrative—Q's coded messages allow for interpretation of events in ways that confirm existing beliefs in other conspiracies (\"false flag shootings, Jewish bankers controlling the world, or the Illuminati\", etc).</li>\n<li>\n<p>Debunking efforts can backfire because of:</p>\n<ul>\n<li><strong>Familiarity</strong>: people can forget the details and end up only remembering the myth, because the information sounds familiar</li>\n<li><strong>Overkill</strong>: processing many (rebuttal) arguments is more cognitively taxing than processing a simple myth, so people might prefer the latter</li>\n<li><strong>Worldview</strong>: confirmation &#x26; disconfirmation bias can be strong, so it's better to target debuking outreach \"towards the undecided majority rather than the unswayable minority.\" Also, try not to be so harsh as to produce psychological resistance—combine worldview-threatening messages with self-affirmation.</li>\n</ul>\n</li>\n<li>People often prefer incorrect mental models over incomplete ones, so you need to fill in the gaps appropriately when debunking a myth.</li>\n</ul>\n<h3>Assignment</h3>\n<blockquote>\n<p><strong>Prepare training data for your first deepfakes clip</strong>. Two sets of ~400 images, one for each face. The faces should be isolated and any “bad” images (blurry, cropped incorrectly, anything obstructing the face) should be removed from the set. Use youtube-dl, ffmpeg, autocrop, and face_recognition tools.</p>\n</blockquote>","frontmatter":{"date":"20 September, 2018","title":"Week 2 deep fakes"},"fields":{"slug":"/blog/itp/faking-the-news/deepfakes/"}}},"pathContext":{"slug":"/blog/itp/faking-the-news/deepfakes/"}}

